import React, { useState, useCallback, useEffect } from 'react';
import { StaticContext } from 'react-router';
import { Link, RouteComponentProps } from 'react-router-dom';
import { GET } from '../utils/requests';
import { TournamentStatus, TournamentSettings, ProfileData, Team } from '../interfaces';
import SectionBreak from '../components/SectionBreak';
import styles from './css/tournaments.module.css';
import { FaAngleLeft, FaAngleRight } from 'react-icons/fa';

export default function Tournaments(props: RouteComponentProps<any, StaticContext, any> & {
	direction: string
	user: ProfileData
	profile: ProfileData & Team
}) {

	const [managed, setManaged] = useState([] as (TournamentStatus & { setting: TournamentSettings })[]);
	const updateManaged = useCallback(() => GET({ url: '/profile/tournaments' })
		.then(setManaged), [setManaged]);
	useEffect(() => {
		if (props.user.username?.toLowerCase() !== props.profile.username?.toLowerCase()) return;
		updateManaged();
	}, [updateManaged, props.user.username, props.profile.username]);

	if (!managed.length) return null;
	
	return (
		<section className={'animated-' + props.direction} key='standings.1'>
			<SectionBreak name='MANAGED TOURNAMENTS'/>
			<div className={['container'].join(' ')}>
				<table className={[styles.tableAnnounce].join(' ')}>
					<thead>
						<tr>
							<th style={{width: '20px' }}>Manage</th>
							<th>Name</th>
							<th>Started</th>
							<th>Round</th>
							<th>View</th>
						</tr>
					</thead>
					<tbody>
						{managed.sort((a, b) => new Date(b.updatedAt).valueOf() - new Date(a.updatedAt).valueOf()).map((tourn, i) => {
							let dateStarted = new Date(tourn.createdAt).toString();
							return (
								<tr key={[tourn.id, i].join('.')} className={styles.rowLink}>
									<td><a className={styles.tournamentLink} href='https://beta.scorchapp.co.uk'>
										<FaAngleLeft />
									</a></td>
									<td>
										{tourn.name || tourn.id}
									</td>
									<td>
										{dateStarted.slice(0, 11)}
									</td>
									<td>
										{tourn.round}
									</td>
									<td>
										{tourn.setting && tourn.setting.public ?
										<Link className={styles.tournamentLink} to={{pathname: '/' + tourn.id, state: {from: props.location.pathname}}}>
											<FaAngleRight />
										</Link>
										: null}
									</td>
								</tr>
							);
						})}				
					</tbody>
				</table>
			</div>
		</section>
	)

}